@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');


*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root{
  --color-bg: #1f1f38;
  --color-bg-variant : #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77,181,255,0.4);
  --color-white:#fff;
  --color-light:rgba(255,255,255,0.6);

  --container-width-lg:75%;
  --container-width-md:86%;
  --container-width-ms:90%;
}

html{
  scroll-behavior: smooth;
  min-height: 100%;
}

::-webkit-scrollbar{
  display: none;
}

#root{
  max-width: 1260px;
  margin: 0 auto;
}

body.light{
  --color-bg : #ECE8DD;
  --color-bg-variant: #E1D7C6 ;
  --color-primary: #579BB1;
  --color-primary-variant: #ECE8DD;
  --color-white: black;
  --color-light: black;
  background-color: #F8F4EA;
  color: black;
  background-blend-mode: hard-light;
}

body{
  font-family: "Poppins",sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/brick-wall-dark.png);
  overflow-x: hidden;
  background-blend-mode: soft-light;
}


.light .header__container{
  background-image: url(/src/assets/WorldMap.svg);

}

.light  header > div > h5.text-light {
  color: black;
}

.light .cta, .light h2, .light .scroll__down {
  
  font-weight: bold;
}

.container{
  /* width: var(--container-width-lg); */
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  height: 100vh;
  scroll-snap-align: start;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light );
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}


.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

.itemdrag{
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 100%;
}

#root > div > canvas {
  position: absolute;
  left: 0;
}

@media screen and (max-width:1024px){
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }
}

@media screen and (max-width:600px){
  .container{
    width: var(--container-width-ms);
  }

  section > h2{
    margin-bottom: 2rem;
  }

  .light .header__container{
    background-image: none;
  }


}



 @media (orientation: landscape)  and (hover: none) and (pointer: coarse) {
  
 section{
  height: auto;
 }
}